#contactPage{
    min-height: calc(100vh-5rem);
    width: 100vw;
    max-width: 50rem;
    margin: 0 auto;
    text-align: center;
    display: flex ;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle{
    /* font: 3rem;
    margin-bottom: 1rem; */
    font-size: 3rem;
    font-weight: 200;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.clientDescription{
    font-weight: 200;
    font-size: 1rem;
}

.clientImgs{
    margin: 1rem 0;
    display: flex;
}



.clientImg{
    object-fit: cover;
    height: 7rem;
    width:  20%;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem;
    flex-wrap: wrap;
}

#clients{
    padding: 2rem;
}

.contactDescription{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm{
     margin: 1rem;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     width: 90vw;
     max-width: 60rem;
}

#name, #email, .message{
    font-size: medium;
      width: 100%;
      max-width: 40rem;
      margin: 0.5rem;
      padding: 0.5rem 1rem;
      color: white;
      border: none;
      border-radius: 0.5rem;
      background: rgb(50,50,50); 
}

.submitBtn{
    background: white;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}

.links{
    display: flex;
    flex-wrap: wrap;
}

.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0  0.75rem;
    border-radius: 50%;
}
#contact{
    min-height: calc(100vh-5rem);

}