#works {
  margin: 0 auto;
  min-height: calc(100vh - 5rem);
  width: 100vw;
  max-width: 50rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 1rem;
}

.worksTitle {
  font-size: 3rem;
  /* font-weight: 600;
  padding-top: 3rem;
  margin-bottom: 1.5rem; */
  margin: 1rem 0;
}

.worksDescription {
  font-weight: 300;
  font-size: 1rem;
  max-width: 45rem;
}

.worksImgs {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100vw;
  max-width: 65rem;
}

.figureCaption {
  margin: 2rem;
}

.worksImg {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  transition: transform 0.3s ease; /* Add a transition for smooth animation */
}

.worksImg:hover {
  transform: scale(1.1); /* Increase the scale on hover to 1.1 (10% bigger) */
}


.caption {
  text-align: left;
  color: white;
}

.caption:hover,
a:hover {
  color: yellow;
  font-size: 1.5rem;
}

a {
  color: white;
}

.worksBtn {
  padding: 0.5rem 2.5rem;
  border: none;
  border-radius: 2rem;
  background: white;
  font-size: 1rem;
}

@media screen and(max-width:585px) {
  .worksImg {
    height: 48vw;
  }
}
