.navbar{
    background: rgb(30 ,30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}
.logo{
    object-fit: cover;
    height: 4rem;
    width: 5em;
}

.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}

.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.desktopMenuBtn{
    background: white ;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 1rem;
}

.desktopMenuBtn:hover{
    /* color: yellow; */
    border: 1px solid yellow;
}

.desktopMenuImg{
    object-fit: cover;
    height: 2rem;
    width: 1 rem;
    margin: 1px 3px;

}

.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.5rem;
}

.navMenu{
    position: absolute;
    top: 5rem;
    right: 2rem;    
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
    }

    .listItem{
        color: white;
        padding: 0.5rem 3rem;
        margin: 0.25rem;
        background: rgb(30 ,30, 30);
    }

    @media screen and (max-width:720px) {
        .mobMenu{
            display: flex;
        }
        .desktopMenu{
            display: none;
        }

        .desktopMenuBtn{
            display: none;
        }
    }